import { __ } from '@wordpress/i18n';
import { InspectorControls, AlignmentToolbar, BlockControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, PanelRow, TextControl, RangeControl, ToggleControl, SelectControl, Dashicon, TextareaControl } from '@wordpress/components';
import produce from 'immer';

// Settings Components
import Title from '../../Components/Title';
import ColorsControl from '../../Components/ColorsControl';
import BtnGroup from '../../Components/BtnGroup';
import IconControl from '../../Components/IconControl';
import { InlineDetailMediaUpload } from '../../Components/MediaControl';
import Typography from '../../Components/Typography';
import SpaceControl from '../../Components/SpaceControl';
import BorderControl from '../../Components/BorderControl';
import MultiShadowControl from '../../Components/MultiShadowControl';
import { tabController } from '../../Components/Helper/functions';

import { targets, buttonTypes, buttonSizes, animationTypes, generalStyleTabs } from './utils/options';

const Settings = ({ attributes, setAttributes }) => {
	const { url, tooltip, target, isDownload, isUpIcon, icon, upIcon, type, isFullWidth, size, animationType, animationDuration, addID, addCSS, alignment, typography, colors, hovColors, padding, border, shadow } = attributes;

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={tabController}>{tab => <>
				{'general' === tab.name && <>
					<PanelBody className='bPlPanelBody help' title={__('Help', 'button-block')} initialOpen={false}>
						{/* <div className='helpItem'>
							<a href='https://bblockswp.com/docs/button-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'button-block')}</a>
						</div> */}

						<div className='helpItem rateUs'>
							<a href='https://wordpress.org/support/plugin/button-block/reviews/#new-post' target='_blank' rel='noreferrer'>
								<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'button-block')}</span>
								<span>{__('We are new and we need your help to grow!', 'button-block')}</span>
							</a>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Button Settings', 'button-block')}>
						<Title className='mb5'>{__('Button Url:', 'button-block')}</Title>
						<TextControl value={url} onChange={val => setAttributes({ url: val })} />
						<small>{__('For download, enter downloadable link', 'button-block')}</small>

						<Title>{__('Tooltip Text:', 'button-block')}</Title>
						<TextControl value={tooltip} onChange={val => setAttributes({ tooltip: val })} />
						<small>{__('If you want to add tooltip on button, type text rather then leave empty.', 'button-block')}</small>

						<PanelRow>
							<Title className=''>{__('Link Target:', 'button-block')}</Title>
							<BtnGroup value={target} onChange={val => setAttributes({ target: val })} isTextIcon={true} options={targets} />
						</PanelRow>

						<ToggleControl className='mt20' label={__('Download Button', 'button-block')} checked={isDownload} onChange={val => setAttributes({ isDownload: val })} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Button Design', 'button-block')} initialOpen={false}>
						<ToggleControl label={__('Upload Icon?', 'button-block')} checked={isUpIcon} onChange={val => setAttributes({ isUpIcon: val })} />

						{isUpIcon ?
							<InlineDetailMediaUpload value={upIcon} types={['image']} onChange={val => setAttributes({ upIcon: val })} placeholder={__('Enter Icon URL', 'button-block')} /> :
							<IconControl value={icon} onChange={val => setAttributes({ icon: val })} defaults={{ class: 'fa fa-paper-plane' }} isSize={false} isColor={false} />}

						<PanelRow className='mt20'>
							<Title className=''>{__('Button Type:', 'button-block')}</Title>
							<BtnGroup value={type} onChange={val => {
								setAttributes({ type: val });
								'flat' === val && setAttributes({ shadow: [] });
								'3d' === val && setAttributes({
									shadow: [
										{ hOffset: '-5px', vOffset: '6px', blur: '2px', spreed: '0px', color: 'rgba(68, 39, 163, 0.57)', isInset: false },
										{ hOffset: '3px', vOffset: '-3px', blur: '0px', spreed: '0px', color: 'rgba(206, 191, 255, 1)', isInset: true }
									]
								});
							}} isTextIcon={true} options={buttonTypes} />
						</PanelRow>

						<ToggleControl className='mt20' label={__('Button Full Width?', 'button-block')} checked={isFullWidth} onChange={val => setAttributes({ isFullWidth: val })} />

						<PanelRow>
							<Title className=''>{__('Button Size:', 'button-block')}</Title>
							<SelectControl value={size} onChange={val => {
								setAttributes({ size: val });

								'small' === val && setAttributes({
									typography: { ...typography, fontSize: 12 },
									padding: { ...padding, vertical: '8px', horizontal: '16px' }
								});
								'medium' === val && setAttributes({
									typography: { ...typography, fontSize: 16 },
									padding: { ...padding, vertical: '12px', horizontal: '24px' }
								});
								'large' === val && setAttributes({
									typography: { ...typography, fontSize: 20 },
									padding: { ...padding, vertical: '14px', horizontal: '30px' }
								});
								'x-large' === val && setAttributes({
									typography: { ...typography, fontSize: 22 },
									padding: { ...padding, vertical: '16px', horizontal: '46px' }
								});
							}} options={buttonSizes} />
						</PanelRow>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Animation', 'button-block')} initialOpen={false}>
						<PanelRow>
							<Title className=''>{__('Type:', 'button-block')}</Title>
							<SelectControl value={animationType} onChange={val => setAttributes({ animationType: val })} options={animationTypes} />
						</PanelRow>

						<Title>{__('Duration (Speed) (s):', 'button-block')}</Title>
						<RangeControl value={animationDuration} onChange={val => setAttributes({ animationDuration: val })} min={0} max={3} step={0.05} />
						<small>{__('Animation duration of speed in seconds')}</small>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Additional', 'button-block')} initialOpen={false}>
						<PanelRow>
							<Title className=''>{__('ID:', 'button-block')}</Title>
							<TextControl value={addID} onChange={val => setAttributes({ addID: val })} placeholder={__('Enter additional ID', 'button-block')} />
						</PanelRow>

						<Title>{__('CSS:', 'button-block')}</Title>
						<TextareaControl value={addCSS} onChange={val => setAttributes({ addCSS: val })} placeholder={__('Enter additional CSS', 'button-block')} />
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Button', 'button-block')}>
						<Typography value={typography} onChange={val => setAttributes({ typography: val })} defaults={{ fontSize: 18, textDecoration: 'none' }} />

						<ColorsControl value={colors} onChange={val => setAttributes({ colors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />

						<ColorsControl label={__('Hover Colors:', 'button-block')} value={hovColors} onChange={val => setAttributes({ hovColors: val })} defaults={{ color: '#fff', bg: '#8344c5' }} />

						<SpaceControl className='mt20' label={__('Padding:', 'button-block')} value={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '10px', horizontal: '20px' }} />

						<BorderControl className='mt20' value={border} onChange={val => setAttributes({ border: val })} defaults={{ radius: '5px' }} />

						<MultiShadowControl label={__('Shadow:', 'team-section')} value={shadow?.shadow || shadow} onChange={val => setAttributes({ shadow: val })} produce={produce} />
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={alignment} onChange={val => setAttributes({ alignment: val })} describedBy={__('Button Alignment')} alignmentControls={[
				{ title: __('Button in left', 'button-block'), align: 'left', icon: 'align-left' },
				{ title: __('Button in center', 'button-block'), align: 'center', icon: 'align-center' },
				{ title: __('Button in right', 'button-block'), align: 'right', icon: 'align-right' }
			]} />
		</BlockControls>
	</>;
};
export default Settings;